import apiService from "@/services/apiService.js";
export default {
  data: () => ({
    dialog: true,
    password: ""
  }),
  asyncData(context) {
    const pass = context.params.resetpassword;
    return {
      returnedToken: pass
    };
  },

  methods: {
    update() {
      this.dialog = false;
      this.$store
        .dispatch("resetPassword", {
          token: this.returnedToken,
          password: { password: this.password }
        })
        .then(() => {
          this.$router.push("/login");
        });
    }
  }
};
